import * as THREE from "three";
import { ThreeScene } from "@/sence/ThreeScene";
import { Box3, BufferAttribute, BufferGeometry, Color, InstancedMesh, Material, Matrix4, Mesh, MeshBasicMaterial, MeshStandardMaterial } from "three";
import { ref } from "vue";
import { App } from "@/App";


export interface Tree {
    id: number;
    label: string;
    children?: Tree[];
}


export class InstanceData {

    public instanceMesh: InstancedMesh;
    public index: number;
    public orignMatrix4:Matrix4;
    private _show:boolean=true
    public set show(value:boolean)
    {
        if(this._show==value) return;
        this._show=value;
        if(value)
        {
            this.instanceMesh.setMatrixAt(this.index,this.orignMatrix4)
        }
        else
        {
            this.instanceMesh.setMatrixAt(this.index, GlbLoaders.zeroMatrix4)
        }
        this.instanceMesh.instanceMatrix.needsUpdate = true;
    }

    public get show():boolean
    {
        return this._show;
    }
}

export class BatchData {

    constructor(mesh: Mesh,batchID: number)
    {
        this.mesh=mesh;
        this.batchID=batchID;
        this.indexs=[]
    }

    public mesh: Mesh;

    public batchID: number;
    public orignPositions:Array<number[]>;
    public orignColors:Array<number[]>;
    public indexs:Array<number>;
    private _show:boolean=true
    public set show(value:boolean)
    {
        if(this._show==value) return;
        this._show=value;
        let positions: BufferAttribute = this.mesh.geometry.attributes.position as BufferAttribute;
        if(this.orignPositions==null)
        {
            this.orignPositions=[]
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                this.orignPositions.push([positions.getX(index),
                    positions.getY(index),
                    positions.getZ(index)])
            }
        }

        if(value)
        {
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                positions.setX(index, this.orignPositions[i][0]);
                positions.setY(index, this.orignPositions[i][1]);
                positions.setZ(index, this.orignPositions[i][2]);
            }
        }
        else
        {
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                positions.setX(index, 0);
                positions.setY(index, 0);
                positions.setZ(index, 0);
            }
        }
        positions.needsUpdate = true;
    }
    public get show():boolean
    {
        return this._show;
    }


    private _color:Color=null;
    public set color(value:Color)
    {


        // if(this.orignColors==null)
        // {
        //     let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
        //     this.orignColors=[]
        //     for (let i = 0; i < this.indexs.length; i++) {
        //         const index = this.indexs[i];
        //         this.orignColors.push([colors.getX(index),
        //             colors.getY(index),
        //             colors.getZ(index)])
        //     }
        // }

        if(value==null)
        {
            let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
            for (let i = 0; i < this.indexs.length; i++) {
                const index = this.indexs[i];
                // colors.setX(index, this.orignColors[i][0]);
                // colors.setY(index, this.orignColors[i][1]);
                // colors.setZ(index, this.orignColors[i][2]);
                colors.setX(index, 1);
                colors.setY(index, 1);
                colors.setZ(index, 1);
            }
            colors.needsUpdate = true;
            return;
        }
        this._color=value;
        let colors: BufferAttribute = this.mesh.geometry.attributes.color as BufferAttribute;
        for (let i = 0; i < this.indexs.length; i++) {
            const index = this.indexs[i];
            colors.setX(index, value.r);
            colors.setY(index, value.g);
            colors.setZ(index, value.b);
        }    
        colors.needsUpdate = true;
    }
    public get color():Color
    {
        return this._color;
    }
}


