import * as THREE from "three"
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls"
import { ThreeRaycaster } from "./ThreeRaycaster";
import { ThreeViewCube } from "./ThreeViewCube";
import elementResizeDetectorMaker from 'element-resize-detector'
import { createBackground } from './vignette/three-vignette.js';
import Stats from 'three/examples/jsm/libs/stats.module'
import { Box3, PMREMGenerator } from "three";
export class ThreeScene {
    public scene: THREE.Scene

    public camera: THREE.PerspectiveCamera


    public renderer: THREE.WebGLRenderer
    public controls: OrbitControls;
    public raycaster: ThreeRaycaster;


    public obj: THREE.Object3D;



    public goldenContainer: HTMLElement

    public vignette: any

    public viewCube:ThreeViewCube;
    

    public stats :Stats;


    private renderEnabled = true // used together with RafHelper


    private pmremGenerator:PMREMGenerator;

   




    constructor(goldenContainer) {
        this.goldenContainer = goldenContainer;
        this.init();
        this.raycaster = new ThreeRaycaster(this.goldenContainer);
    }
    private init() {
        this.initRenderer();
        this.initGPURenderer();
        this.initScene();
        this.initCamera();
        this.initLight();
        this.initControls();
        this.initStats();
        this.initEnvironment("environment/footprint_court_2k.hdr")
        this.viewCube=new ThreeViewCube(this.goldenContainer);
        const erd = elementResizeDetectorMaker();
        erd.listenTo(this.goldenContainer,(element)=>{
            this.onWindowResize()
        })
        this.animate();
    }
    private onWindowResize = () => {
        this.camera.aspect = this.goldenContainer.clientWidth / this.goldenContainer.clientHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(this.goldenContainer.clientWidth, this.goldenContainer.clientHeight);
        //this.pickRender.setSize(this.goldenContainer.clientWidth, this.goldenContainer.clientHeight);
        //正交相机
        this.viewCube.cameraOrtho.left = -this.viewCube.frustumSize;
        this.viewCube.cameraOrtho.right = this.viewCube.frustumSize;
        this.viewCube.cameraOrtho.top = this.viewCube.frustumSize;
        this.viewCube.cameraOrtho.bottom = -this.viewCube.frustumSize;
        this.viewCube.cameraOrtho.updateProjectionMatrix();

        
    }
    /**
     * 场景
     */
    private initScene() {
        this.scene = new THREE.Scene();
        this.obj=new THREE.Object3D();
        this.scene.add(this.obj);
    }

    /**
     *  初始化相机
     */
    private initCamera() {

        this.camera = new THREE.PerspectiveCamera (45, 1, 1, 5000);
        this.camera.aspect =this.goldenContainer.clientWidth/ this.goldenContainer.clientHeight;
        this.camera.position.set(0, 40, 40);
        this.scene.add( this.camera );
    }


    /**
     *  初始渲染器
     */
    private initRenderer() {
        // this.renderer = new THREE.WebGLRenderer({
        //     antialias: true
        // });
         //curCanvas = document.createElement('canvas');
        // this.goldenContainer.getElement().get(0).appendChild(curCanvas);
        this.renderer = new THREE.WebGLRenderer({antialias: true });
        this.renderer.setSize(this.goldenContainer.clientWidth, this.goldenContainer.clientHeight);
        this.goldenContainer.appendChild(this.renderer.domElement)
         //背景色
         //this.renderer.setClearColor(0xcccccc);
         //屏幕物理像素和px比率
         this.renderer.setPixelRatio(window.devicePixelRatio);
         //threejs的色彩空间渲染方式（重要）
         this.renderer.outputEncoding =THREE.sRGBEncoding;
         this.renderer.physicallyCorrectLights = true;
         //阴影
         //this.renderer.shadowMap.enabled = true;
    }

    private initGPURenderer()
    {
    }


    private initLight() {
        
        const light1  = new THREE.AmbientLight(0xffffff, 1);
        this.camera.add( light1 );
        let light2 = new THREE.DirectionalLight(0xffffff, 1*Math.PI);
        light2.position.set(0.5, 0, 0.866); // ~60º
        this.camera.add( light2 );
    }

    /**
     * 控制器
     */
    private initControls() {
        //初始化轨迹球控件
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        // 使动画循环使用时阻尼或自转 意思是否有惯性
        this.controls.enableDamping = true;
        //动态阻尼系数 就是鼠标拖拽旋转灵敏度
        this.controls.dampingFactor = 0.5;
        //是否可以缩放
        this.controls.enableZoom = true;
        //是否自动旋转
        this.controls.autoRotate = false;
        //设置相机距离原点的最近距离
        //controls.minDistance = 50;
        //设置相机距离原点的最远距离
        //controls.maxDistance = 200;
        //是否开启右键拖拽
        //this.controls.enablePan = true;
        this.controls.mouseButtons = {
            LEFT: -1,
            MIDDLE: THREE.MOUSE.PAN,
            RIGHT: THREE.MOUSE.ROTATE,
        }

        this.controls.addEventListener('change', () => {
            this.enableRender()
        })
    }


    /**
     * 渲染
     */
    public render() {
        
        this.renderer.autoClear = false;
        this.renderer.setViewport(0, 0, this.goldenContainer.clientWidth,  this.goldenContainer.clientHeight);
        this.renderer.render(this.scene, this.camera);
        //复制主场景相机的信息，位置，四元数等
        this.viewCube.cameraOrtho.position.copy(this.camera.position);
        this.viewCube.cameraOrtho.position.sub(this.controls.target);
        this.viewCube.cameraOrtho.lookAt(this.scene.position);
        let dir= this.viewCube.cameraOrtho.position.normalize();
        this.viewCube.cameraOrtho.position.x=dir.x*50;
        this.viewCube.cameraOrtho.position.y=dir.y*50;
        this.viewCube.cameraOrtho.position.z=dir.z*50;
        this.renderer.setViewport(this.goldenContainer.clientWidth-170,this.goldenContainer.clientHeight-170, 120, 120)
        this.renderer.render(this.viewCube.sceneOrtho, this.viewCube.cameraOrtho);
        //this.controls.update();
    }



    private initStats()
    {
        this.stats=Stats();
        this.stats.setMode(0);
        this.stats.domElement.style.position = 'absolute';
        this.stats.domElement.style.left = '0px';
        this.stats.domElement.style.top = '0px';
        this.goldenContainer.appendChild(this.stats.domElement);
    }


   
    private animate = () => {
        requestAnimationFrame(this.animate)
        if(this.renderEnabled)
        {
            this.render();
            this.renderEnabled=false;
        }
        this.stats.update();
        //this.vignette.style({aspect: this.camera.aspect});
    }


    /**
     * //设置模型到适合观察的大小
     */
    public setCameraLookCenter() {
        //计算包围盒
        let boxHelper = new THREE.BoxHelper(this.obj);
        boxHelper.geometry.computeBoundingBox();
        let radius = boxHelper.geometry.boundingSphere.radius;
        //计算相机位置
        let box = boxHelper.geometry.boundingBox;
        let center = new THREE.Vector3((box.max.x + box.min.x) / 2, (box.max.y + box.min.y) / 2, (box.max.z + box.min.z) / 2);
        let cameraPos = new THREE.Vector3(
            center.x,
            center.y + (box.max.y - box.min.y) / 2,
           center.z + 2.2 * radius
        );
        this.camera.lookAt(center );
        this.camera.position.copy(cameraPos);
        this.controls.target = center ;
        this.controls.update();
    }

    private _senceBox:THREE.Box3=new Box3;

    public setCameraByBox(box:THREE.Box3) {
        this._senceBox=box;
        let x = box.max.x - box.min.x;
        let y = box.max.y - box.min.y;
        let z = box.max.z - box.min.z;
        let radius = Math.max(x, y, z) / 2;
        //计算相机位置\
        let center = new THREE.Vector3((box.max.x + box.min.x) / 2, (box.max.y + box.min.y) / 2, (box.max.z + box.min.z) / 2);
        let cameraPos = new THREE.Vector3(
            center.x,
            center.y + (box.max.y - box.min.y) / 2,
            center.z + 2.2 * radius
        );
        this.camera.lookAt(center);
        this.camera.position.copy(cameraPos);
        this.controls.target = center;
        this.controls.update();
    }

    public clearObject() {
        this.obj.clear();
    }


    public getIntersect(event): THREE.Vector3 {
        return this.raycaster.getIntersect(event)
    }


    public enableRender ()  {
        this.renderEnabled = true;
    }


    private initEnvironment(path): Promise<THREE.Texture> {

        this.vignette  = createBackground({
            aspect: this.camera.aspect,
            //grainScale: false ? 0 : 0.001, // mattdesl/three-vignette-background#1
            grainScale:0.001,
            colors: ["#ffffff", '#4b4b4b']
        });
        this.vignette.name = 'Vignette';
        this.vignette.renderOrder = -1;
        this.scene.add(this.vignette)

        return null
        // if (!path) return Promise.resolve(null);
        // if(this.pmremGenerator ==null)
        // {
        //     this.pmremGenerator = new PMREMGenerator(this.renderer);
        //     this.pmremGenerator.compileEquirectangularShader();
        // }
        // return new Promise((resolve, reject) => {
        //     new RGBELoader().load(path, (texture) => {
        //             const envMap = this.pmremGenerator.fromEquirectangular(texture).texture;
        //             this.pmremGenerator.dispose();
        //             this.scene.environment = envMap;
        //             //this.scene.background =  envMap;
        //             resolve(envMap);
        //         }, undefined, reject);
        // });
    }

    
}